$text-color: #f5f5f5;
$background-color: #3a3a3f;

body {
  background-color: white;
}
.home {
  background: white;
  margin-bottom: 10px;
}

.area {
  background: $background-color;
  width: 100%;
  height: 94vh;

  .titleHome {
    padding-top: 165px;
    text-align: center;
    font-size: 50px;
    p {
      color: $text-color;
    }
  }

  .circles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 91%;
    overflow: hidden;
    padding: 0px;
  }

  .circles li {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    animation: animate 25s linear infinite;
    bottom: -150px;
  }

  .circles li:nth-child(1) {
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
  }

  .circles li:nth-child(2) {
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
  }

  .circles li:nth-child(3) {
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
  }

  .circles li:nth-child(4) {
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
  }

  .circles li:nth-child(5) {
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
  }

  .circles li:nth-child(6) {
    left: 10%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
  }

  .circles li:nth-child(7) {
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
  }

  .circles li:nth-child(8) {
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
  }

  .circles li:nth-child(9) {
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
  }

  .circles li:nth-child(10) {
    left: 60%;
    width: 110px;
    height: 110px;
    animation-delay: 0s;
    animation-duration: 19s;
  }

  .circles li:nth-child(11) {
    left: 35%;
    width: 20px;
    height: 20px;
    animation-delay: 7s;
  }
}
@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 0;
  }

  100% {
    transform: translateY(-1000px) rotate(720deg);
    opacity: 0;
    border-radius: 50%;
  }
}

@media screen and (max-width: 479px) {
  .area {
    height: 100vh;

    .titleHome {
      padding-top: 240px;
      text-align: center;
      font-size: 40px;
      p {
        color: $text-color;
      }
    }

    .circles {
      margin: 0px;
      height: 105%;
    }
    .circles li:nth-child(6) {
      display: none;
    }
    .circles li:nth-child(7) {
      display: none;
    }
    .circles li:nth-child(9) {
      width: 35px;
      height: 35px;
    }
    .circles li:nth-child(10) {
      width: 60px;
      height: 60px;
    }
  }
}